import PropTypes from '../../_util/vue-types'

export default {
  props: {
    children: PropTypes.func.def(() => null),
  },
  methods: {
    getRef (name) {
      return this[name]
    },

    saveRef (name) {
      return (node) => {
        if (node) {
          this[name] = node
        }
      }
    },
  },

  render () {
    // newfunction
    const saveRef = (name) => this.saveRef(name)
    const getRef = (name) => this.getRef(name)
    return this.children(saveRef, getRef)
  },
}



